import React, { Component } from "react";
import AuthService from "../services/auth.service";
import { Redirect } from 'react-router-dom';
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import CheckButton from "react-validation/build/button";
import Header from './Header';
import Menu from './Menu';
import Footer from './Footer';
import axios from 'axios'




const required = value => {
  if (!value) {
    return (
      <div className="alert alert-danger" role="alert">
        This field is required!
      </div>
    );
  }
};

export default class AddSurvey extends Component {

  constructor(props) {
    super(props);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.onChangeTitle = this.onChangeTitle.bind(this);
    this.onChangeSurveyCategory = this.onChangeSurveyCategory.bind(this);
    this.onChangeStatus = this.onChangeStatus.bind(this);
    this.onChangeOrigin = this.onChangeOrigin.bind(this);

    this.state = {
      title: "",
      category: "",
      status: "",
      origin: "",
      loading: false,
      message: ""
    };
  }

  onChangeTitle(e) {
    this.setState({
      title: e.target.value
    });
  }

  onChangeSurveyCategory(e) {
    this.setState({
      category: e.target.value
    });
  }
  onChangeStatus(e) {
    this.setState({
      status: e.target.value
    });
  }

  onChangeOrigin(e) {
    this.setState({
      origin: e.target.value
    });
  }


  handleSubmit(e) {
    e.preventDefault();

    this.setState({
      message: "",
      successful: false,
      loading: true,
      meloading: true

    });

    this.form.validateAll();

    if (this.checkBtn.context._errors.length === 0) {
      AuthService.CreateSurvey(
        this.state.title,
        this.state.category,
        this.state.status,
        this.state.origin, 
      ).then(
        response => {
          this.setState({
            message: response.data,
            successful: true,
            loading: false,
            meloading: false
          });
          window.location = "/formBuilder"
        },

        error => {
          const resMessage =
            (error.response &&
              error.response.data.detail &&
              error.response.data.detail) ||
            error.message ||
            error.toString();

          this.setState({
            successful: false,
            message: resMessage,
            loading: false,
            meloading: false
          });
        }
      );
    }
  }

  componentWillMount() {


    const username = 'admin'
    const password = 'Pass@1234'
    const token = Buffer.from(`${username}:${password}`, 'utf8').toString('base64')
    axios.get("https://ecological.chinikiguard.com/projects/api/list/?all_record=1",
      {
        headers:
        {
          'Authorization': `Basic ${token}`,
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
          'Access-Control-Allow-Credentials': true
        },

      })
      .then(res => {
        if (res.data) {
          localStorage.setItem("AllProjectData", JSON.stringify(res.data));



        }
        // console.log(res);
        // console.log(res.data);
        // window.location = "/dashboard"
      })

  }


  render() {

    if (!localStorage.getItem('user')) {
      return (<Redirect to={'/'} />)
    }

    const projectId = JSON.parse(localStorage.getItem('AllProjectData'))

    const { loading } = this.state;

    // if (this.state.redirectToReferrer) {
    //     return (<Redirect to={'/dashboard'} />)
    // }

    // if (sessionStorage.getItem('token')) {
    //     return (<Redirect to={'/dashboard'} />)
    // }

    return (
      <div>
        <Header />

        <div className="content-wrapper" style={{ minHeight: '2120.88px' }}>
          <Menu />
          {/* Content Header (Page header) */}
          <section className="content-header">
            <div className="container-fluid">
              <div className="row mb-2">

              </div>
            </div>{/* /.container-fluid */}
          </section>
          {/* Main content */}
          <section className="content">
            <div className="container-fluid">
              <div className="row">
                <div className="col-md-12">


                  <div className="card card">
                    <div className="card-header">
                      <h1 className="card-title">SURVEY</h1>
                    </div>

                    <div className="card-body">
                      <Form onSubmit={this.handleSubmit} ref={c => { this.form = c; }} className="form" id="">

                        {!this.state.successful && (
                          <div>
                            <label>Title</label>
                            <Input className="form-control" type="text" placeholder="" 
                            value={this.state.title}
                            onChange={this.onChangeTitle}
                            validations={[required]}
                            />

                            <br />
                            <div className="form-group  form-group-lg">
                              <label>Select Category</label>
                              <select className="form-control"
                              value={this.state.category}
                              onChange={this.onChangeSurveyCategory}
                              validations={[required]}
                              >

                                <option>--select--</option>
                                <option value="1">Lg monthly tax Return</option>

                                {/* {projectId.map(project => (

                                  <option value={`${project.pk}`}>{project.title}</option>
                                ))} */}

                              </select>
                            </div>

                            <div className="form-group  form-group-lg">
                              <label>Status</label>
                              <select className="form-control"
                                value={this.state.status}
                                onChange={this.onChangeStatus}
                                validations={[required]}
                              >
                                <option value="">--Select--</option>
                                <option value="open">Open</option>
                                <option value="closed">Closed</option>
                              </select>
                            </div>
                            <div className="form-group  form-group-lg">
                              <label>Origin</label>
                              <select className="form-control"
                              value={this.state.origin}
                              onChange={this.onChangeOrigin}
                              validations={[required]}
                              >
                                <option value="">--Select--</option>
                                <option value="internal">internal</option>
                                <option value="external">external</option>
                              </select>
                            </div>
                            <br />
                            <div className="form-group">
                              <button type="submit" className="btn btn-primary btn-lg float-right"
                              disabled={this.state.loading}
                              >
                              {this.state.loading && (
                            <span className="spinner-border spinner-border-sm"></span>
                                        )}
                            <span>Submit</span>
                                </button>
                                <CheckButton
                                      style={{ display: "none" }}
                                      ref={c => {
                                        this.checkBtn = c;
                                      }}
                                    />
                            </div>
                            <center>
                              {this.state.message && (

                                <div className="pb-5" >
                                  <div
                                    className={
                                      this.state.successful
                                        ? "alert alert-custom alert-outline-success fade show mb-5"
                                        : "alert alert-custom alert-outline-danger fade show mb-5"
                                    }
                                    role="alert"
                                  >
                                    {this.state.message}
                                  </div>
                                </div>
                              )}</center>
                          </div>


                        )}
                      </Form>
                    

                    </div>
                    {/* /.card-body */}
                  </div>

                </div>

              </div>
              {/* /.row */}
            </div>{/* /.container-fluid */}
          </section>
          {/* /.content */}

        </div>
        <Footer />

      </div>


    );
  }
}
